import { Trans } from '@lingui/macro'
import React from 'react'
import Select from 'react-select'
import { Props } from 'react-select/src/Select'
import { StylesConfig } from 'react-select/src/styles'

import { Colors, Spacing } from '../styling'

export type TSelectControlProps<OptionType> = NoChildren &
  Props<OptionType> & {
    width?: number
    minWidth?: number
    maxHeight?: number
    invalid?: boolean
    hideOnSingleOption?: boolean
  }

function getControlStyles({
  width,
  minWidth,
  invalid,
}: TSelectControlProps<unknown>) {
  const colors =
    invalid === true
      ? {
          border: `1px solid ${Colors.red}`,
          backgroundColor: Colors.errorBackground,
        }
      : {
          border: `1px solid ${Colors.greyMiddle}`,
        }
  return {
    ...colors,
    margin: Spacing.MarginAroundFormComponents,
    width: width !== undefined ? `${width}rem` : '100%',
    minWidth: minWidth !== undefined ? `${minWidth}rem` : '8rem',
  }
}

export const composeSelectControlStyles = (outerStyles: StylesConfig = {}) => ({
  ...outerStyles,
  control: (base, state) => {
    const controlStyles = { ...base, ...getControlStyles(state.selectProps) }
    return outerStyles.control
      ? outerStyles.control(controlStyles, state)
      : controlStyles
  },
  menu: (base, state) => {
    const {
      selectProps: { width },
    } = state
    const menuStyles = {
      ...base,
      zIndex: 20,
      color: Colors.black,
      width: width !== undefined ? `${width}rem` : '100%',
    }
    return outerStyles.menu ? outerStyles.menu(menuStyles, state) : menuStyles
  },
  valueContainer: (base, state) => {
    const {
      selectProps: { maxHeight },
    } = state
    const valueContainerStyles = {
      ...base,
      maxHeight: maxHeight !== undefined ? `${maxHeight}rem` : '100%',
      overflowY: maxHeight !== undefined ? 'scroll' : 'hidden',
    }
    return outerStyles.valueContainer
      ? outerStyles.valueContainer(valueContainerStyles, state)
      : valueContainerStyles
  },
  clearIndicator: base => ({
    ...base,
    cursor: 'pointer',
  }),
})

export function SelectControl<OptionType>({
  hideOnSingleOption = false,
  ...props
}: TSelectControlProps<OptionType>) {
  if (hideOnSingleOption && props.options && props.options.length <= 1) {
    return null
  }

  return (
    <Select<OptionType>
      {...props}
      components={{
        ClearIndicator,
        ...props.components,
      }}
      styles={composeSelectControlStyles(props.styles)}
    />
  )
}

function ClearIndicator(props) {
  const {
    getStyles,
    innerProps: { ref, ...restInnerProps },
  } = props
  return (
    <div
      {...restInnerProps}
      ref={ref}
      style={getStyles('clearIndicator', props)}
    >
      <div style={{ padding: '0px 5px' }}>
        <Trans>All</Trans>
      </div>
    </div>
  )
}
